<template>
    <div class="col-12 col-sm-4 col-md-3 col-lg-2 mb-3 mb-3">
        
        <div class="card">
            <img :src="producto.thumbnailUrl" :alt="`${producto.title}`" class="img-card-top">
            <div class="card-body">
                <h5 class="card-title">{{producto.title}}</h5>
                <p class="card-text">$<span>{{producto.precio}}</span></p>
                <button class="btn btn-dark" @click='addproduct(producto)'>Comprar</button>
            </div>
        </div>
    </div>
</template>

<script>
import {useStore} from 'vuex'
export default {
    props:['producto'],
    setup(){
        const store=useStore();
        const addproduct= producto  =>{
            store.dispatch('addToCart',producto)
        }
        return {addproduct}
    }
}
</script>
